import { Image, Grid, Divider, Header } from "semantic-ui-react";
const Playlist = ({ playlist }) => (
  <React.Fragment>
    <Divider />
    <Grid inverted>
      <Grid.Row columns={2}>
        <Grid.Column>
          <Image
            src={playlist.snippet.thumbnails.medium.url}
            size="small"
            verticalAlign="top"
          />
        </Grid.Column>
        <Grid.Column>
          <Header>{playlist.snippet.title}</Header>
        </Grid.Column>
      </Grid.Row>
    </Grid>

    <span>Top Aligned</span>
    <div>Top Aligned</div>
  </React.Fragment>
);

export default React.memo(Playlist);
